.MobileFooter {
    width: 100%;
    height: 100%;
    display: none;
    position: relative;
    z-index: 85;
    margin-top: 40px;
}



.MobileFooter-Wrapper {
    width: 100%;
    height: 100%;
    z-index: 5;
}

.MobileFooter-Tab {
    width: 100%;
    height: 60px;
    border-bottom: 1px solid #d5d5d5;
    display: flex;
    align-items: center;

    .MobileFooter-TabTitle {
        font-size: 14px;
        font-weight: 500;
        color: rgb(184, 184, 184);
        cursor: pointer;
    }

    .MobileFooter-AdditionSymbol {
        color: rgb(184, 184, 184);
        font-weight: 600;
        font-size: 16px;
        cursor: pointer;
        padding-bottom: 3px;
        margin-left: 40px;
        padding-right: 10px;
    }
}

.MobileFooter-TabLinks {
    width: 100%;
    padding-bottom: 20px;
    display: none;

    .MobileFooter-TabLinksList {
        margin-left: 40px;
        list-style-type: none;

        .MobileFooter-TabLinksListItem {
            font-size: 14px;
            font-weight: 600;
            padding: 20px 0;
        }
    }
}

.MobileFooterLinks-Open {
    display: block;
}


@media screen and (max-width: 1000px) {
    .MobileFooter {
        display: block;

    }
}
